@import url('https://fonts.googleapis.com/css2?family=Jaro:opsz@6..72&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Poppins", sans-serif;
}
:root{
    --color1: #222831;
    --color2: #31363F;
    --color3: #F8F4EC;
    --color4: #E1F7F5;
    --color5: #F8EDFF;
    
}

@media screen and (max-width: 480px){
    footer{
        background-color: var(--color1);
        min-height: 40dvh;
   }
   .footer-container{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        min-height: 40dvh;
   }
   .footer-title{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
   }
   .footer-title h1{
        font-family: "Jaro", sans-serif;
        font-optical-sizing: auto;
        font-weight: 400;
        font-style: normal;
        font-size: 3rem;
        padding-top: 1rem;
   }
   .social{
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     gap: 1rem;
   }
   .social h5{
     color: var(--color3);
   }
   .socials{
     display: flex;
     flex-wrap: wrap;
     justify-content: center;
     align-items: center;
     gap: 2.5rem;
   }
   .socials a{
     font-size: 1.5rem;
     color: var(--color3);
   }
   .footer-copyright{
        margin-bottom: 5rem;
   }
   .footer-copyright p{
          font-size: .8rem;
   }
}

